/* http://prismjs.com/download.html?themes=prism-okaidia&languages=markup+css+clike+javascript+bash+jsx&plugins=autolinker */
/**
 * okaidia theme for JavaScript, CSS and HTML
 * Loosely based on Monokai textmate theme by http://www.monokai.nl/
 * @author ocodia
 */

 /* Edited by BootstrapBay */

 code[class*="language-"],
 pre[class*="language-"] {
   color: #212529;
   background: none;
   /* text-shadow: 0 1px rgba(0, 0, 0, 0.3); */
   text-align: left;
   white-space: pre;
   word-spacing: normal;
   word-break: normal;
   word-wrap: normal;
	 font-size: .9rem;
	 font-weight: 500;
	 line-height: 1.5rem;

   -moz-tab-size: 4;
   tab-size: 4;

   -webkit-hyphens: none;
   -ms-hyphens: none;
   hyphens: none;
 }

 /* Code blocks */
 pre[class*="language-"] {
   padding: 0em 1em;
   margin: 1.5em 0;
   overflow: auto;
 }

 :not(pre) > code[class*="language-"],
 pre[class*="language-"] {
   background: #F7F7F7;
 }

 /* Inline code */
 :not(pre) > code[class*="language-"] {
   padding: .1em;
   border-radius: .3em;
   white-space: normal;
 }

 .token.comment,
 .token.prolog,
 .token.doctype,
 .token.cdata {
   color: slategray;
 }

 .token.punctuation {
   color: #212529;
 }

 .namespace {
   opacity: .7;
 }

 .token.property,
 .token.tag,
 .token.constant,
 .token.symbol,
 .token.deleted {
   color: #611F6A;
 }

 .token.boolean,
 .token.number {
   color: #071D5C;
 }

 .token.selector,
 .token.attr-name,
 .token.string,
 .token.char,
 .token.builtin,
 .token.inserted {
   color: #007A5B;
 }

 .token.operator,
 .token.entity,
 .token.url,
 .language-css .token.string,
 .style .token.string,
 .token.variable {
   color: #1D1C1D;
 }

 .token.atrule,
 .token.attr-value,
 .token.function {
   color: #E01E5B;
 }

 .token.keyword {
   color: #1264A4;
 }

 .token.regex,
 .token.important {
   color: #E01E5B;
 }

 .token.important,
 .token.bold {
   font-weight: bold;
 }
 .token.italic {
   font-style: italic;
 }

 .token.entity {
   cursor: help;
 }

 .token a {
   color: inherit;
 }

code .tag {
  font-size: inherit;
  padding: 0;
  display: inherit;
}

